.coverImageContainer {
  position: relative;
  height: 60vh; // Set the container height to 50% of the viewport height
}

.mainheading {
  font-size: 32px;
  font-weight: 600;
  font-family: "Outfit";
  line-height: 1.7;
  margin-bottom: 16px;
  margin-top: 16px;
}

.subheadingdata {
  color: #5a7184;

  h1,
  h1 strong {
    font-size: 26px;
    font-weight: 600;
    margin-top: 20px;
    margin-bottom: 10px;
  }

  h2,
  h2 strong {
    font-size: 24px;
    font-weight: 500;
    margin-top: 20px;
    margin-bottom: 10px;
  }

  h3,
  h3 strong {
    font-size: 20px;
    font-weight: 500;
    margin-top: 20px;
    margin-bottom: 10px;
  }

  h4,
  h4 strong {
    font-size: 18px;
    font-weight: 500;
    margin-top: 20px;
    margin-bottom: 10px;
  }

  p {
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 5px;
  }

  img {
    width: 100%;
    height: auto;
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 10px;
  }
}

.coverImageContainer img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 60vh;
  object-fit: cover;
}

.svgOverlay {
  position: absolute;
  top: 70%;
  left: 0;
  width: 100%;
  height: 50px;
  z-index: 1;
}

.titleOverlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: white;
  font-size: 2rem;
  font-weight: bold;
  width: 100%;
  padding: 10px;
  z-index: 2;
}

.blog_content {
  display: flex;
  flex-direction: row;
  align-items: start;
  justify-content: center;
  margin-top: 7%;
  width: 100%;
  gap: 65px;

  @media screen and (max-width: 950px) {
    display: flex;
    flex-direction: column;
  }

  .span {
    display: flex;
    flex-direction: column;
    gap: 10px;

    @media screen and (max-width: 950px) {
      display: flex;
      flex-direction: row;
    }
  }

  .span2 {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-self: end;
    margin-top: 100px;

    @media screen and (max-width: 950px) {
      display: flex;
      flex-direction: row;
    }
  }

  .summary {
    color: #5a7184;
    font-size: 20px;
  }

  .imageonsummary {
    margin-top: 30px;
    height: 392px;
    object-fit: cover;
    border-radius: 20px;
  }

  img {
    width: 100%;
  }

  .imagecaption {
    text-align: center;
    color: #5a7184;
  }
}

.authorPhoto {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 10px;
}

.authorSection {
  margin-top: 20px;
  display: flex;
  width: 100%;
  align-items: center;

  div {
    display: flex;
    flex-direction: column;
  }

  .authorPhoto {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 10px;
  }

  .groupAuthorNameSection {
    display: flex;
    flex-direction: row;

    .name {
      display: flex;
      justify-content: start;
      align-items: start;
    }

    .date {
      margin-left: auto;
      text-align: end;
      display: flex;
      align-items: center;
    }

    .authorName {
      font-weight: bold;
      font-size: 16px;
      color: #183b56;
    }

    .verified {
      color: #000;
      font-weight: 100;
      font-size: 13px;
      display: flex;
      align-items: center;
      margin-top: 4px;
    }

    .icons {
      width: 17.297px;
      height: 16px;
    }
  }
}

.sliderImage {
  margin-top: 30px;
  height: 392px;
  object-fit: cover;
  border-radius: 20px;
}

@media screen and (min-width: 0) and (max-width: 500px) {
  .titleOverlay {
    top: 55%;
  }
}

@media screen and (max-width: 3150px) {
  .subcontainer {
    width: 750px;

    .groupAuthorNameSection {
      width: 750px;
    }
  }
}

@media screen and (max-width: 950px) {
  .subcontainer {
    width: 100%;

    .groupAuthorNameSection {
      width: 100%;
    }
  }
}

@media screen and (max-width: 450px) {
  .titleOverlay {
    font-size: 20px;
    font-weight: 400;
  }
}

//grwuuywf
.getTouch {
  margin-top: 50px;
  background: #111116;

  .wave_svg {
    width: 100%;
    height: auto;
    fill: #111116;
  }

  .group {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -101px;
    width: 100%;
  }

  .textempower {
    display: flex;
    align-items: center;
    color: #ffff;
    font-size: 25px;
    font-weight: 700;
    line-height: 48px;
    letter-spacing: 0.2px;

    @media screen and (max-width: 600px) {
      font-size: 20px;
      line-height: 20px;
    }
  }

  .textjoin {
    font-size: 12px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0px;
    text-align: left;
    color: #5a7184;

    @media screen and (max-width: 600px) {
      font-size: 16px;
      line-height: 20px;
    }
  }

  .email {
    margin-top: -101px;
    width: 100%;
    padding: 100px 0;
    flex-shrink: 0;
    background: #111116;
    justify-content: center;
    display: flex;
    gap: 20px;
    align-items: center;

    .emailgroup {
      display: flex;
      gap: 20px;
      flex-direction: column;
      text-align: center;
      width: 50%;
    }

    .emailBox {
      display: flex;
      gap: 10px;

      input {
        padding: 10px;
        border: 1px solid #ccc;
        border-radius: 5px;
      }

      .getStartedButton {
        background-color: blue;
        color: white;
        padding: 10px;
        width: 130px;
        border: none;
        border-radius: 5px;
        cursor: pointer;
      }
    }

    .cardWave {
      // display: flex;
      align-items: center;
      justify-content: center;
      width: 50%;
      position: relative;

      .blackbox1 {
        width: 25%;
        height: 60%;
        position: absolute;
        right: 52%;
        top: 45%;
        border-radius: 10px;
        background-color: #303030;

        @media screen and (max-width: 860px) {
          left: 6% !important;
          top: 50%;
          width: 150px !important;
          height: 150px !important;
        }
      }

      .blueBox1 {
        width: 25%;
        height: 60%;
        border-radius: 10px;
        position: absolute;
        right: 23%;
        background-color: #4b9bee;
        margin-top: 1.8em;

        @media screen and (max-width: 860px) {
          right: 9% !important;
          width: 150px !important;
          height: 150px !important;
        }
      }
    }

    .maincard {
      margin-bottom: 20px;
      width: 100%;
      display: flex;
      align-items: center;
      flex-direction: column;
      position: relative;

      .card {
        max-width: 100%; // Adjust as needed
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
        display: flex;
        justify-content: center;
        flex-direction: column;
        background-color: #ffff;
        z-index: 0;
        border-radius: 10px;

        .cardnamediv {
          height: auto;
          padding: 10px;
        }

        img {
          padding: 5px;
          max-width: 100%;
          height: auto;
          border-radius: 12px 12px 0 0;
        }

        h3 {
          // padding: 12px;
          margin-bottom: 8px;
        }

        p {
          color: #555;
        }
      }
    }
  }
}

@media screen and (max-width: 5000px) {
  .search_container {
    max-width: 500px;
  }

  input {
    width: 230px;
  }

  .card {
    width: 50%;

    .cardnamediv {
      height: 100px;
    }
  }

  .options {
    position: relative;
    left: 120px;
  }
}

// Media Query for 1100px (Tablet)
@media screen and (max-width: 1100px) {
  .main_container {
    padding: 0 20px;

    .first_part {
      align-items: center;

      .text_section {
        text-align: center;
      }

      .search_section {
        flex-direction: column;
      }
    }
  }

  .email {
    width: 100%; // Take full width on smaller screens

    .card {
      margin-left: 20px;
    }
  }
}

@media screen and (max-width: 860px) {
  .options {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    left: 0;
    margin-bottom: 50px;
  }

  .email {
    width: 100%; // Take full width on smaller screens
    display: flex;
    margin-top: 20px;
    flex-direction: column;

    .emailgroup {
      width: 80% !important;
    }

    .cardWave {
      width: 80% !important;
    }

    .emailBox {
      input {
        width: 100%; // Take full width on smaller screens
      }
    }

    .card {
      width: 70%; // Take full width on smaller screens
      margin-left: 0; // Remove left margin

      .cardnamediv {
        height: auto;
      }
    }
  }
}

// Media Query for 600px (Mobile)
@media screen and (max-width: 600px) {
  .main_container {
    padding: 0 20px;

    .first_part {
      align-items: center; // Center the text in the first part

      .search_container {
        margin-top: 10px !important;
        max-width: 320px;
      }
    }
  }

  .more {
    padding-bottom: 1em;
  }

  .second_part {
    flex-direction: column;
    align-items: center;

    .article_card {
      width: 100%;
    }
  }

  .email {
    width: 100%; // Take full width on smaller screens
    display: flex;
    margin-top: 20px;
    flex-direction: column;

    .emailBox {
      input {
        width: 100%; // Take full width on smaller screens
      }
    }
  }
}

@media screen and (max-width: 355px) {
  .search_container {
    margin-top: 20px !important;
    max-width: 120px;
  }
}

@media screen and (max-width: 2055px) {
  .svgOverlay {
    width: 100%;
    margin-top: 1em;
  }
}